import React, {Component} from 'react';
import '../assets/css/aboutme.css';
import AboutmeContent from '../components/AboutmeContent'

class Aboutme extends Component {
    render() {
        return (
          <div class="about_me_container" id="about_me_container">
            <h1 class="about_me">Project</h1>
            <hr class="hr_about_me"></hr>
            <div class="inner_container">
              <div class="about_me_img">
              </div>
              <div class="about_me">
              <span class="name">
                이 현호
                </span>
                <div id="content">
                <p>
                  <AboutmeContent />
                </p>
                </div>
                <div class="sns_tag">
                  <a class="github" href="https://github.com/leehyeonho"><i class="fa fa-github fa-5x" ></i></a>
                  <a class="google" href="mailto:dlgusgh0812@gmail.com"><i class="fa fa-google fa-3x" ></i></a>

                </div>
              </div>
            </div>
          </div>

        );
    }
}

export default Aboutme;
